import React,{useEffect} from 'react'
import styled from '@emotion/styled'
import "aos/dist/aos.css";
import AOS from 'aos';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';
import breakPoint from '../utils/breakPoint'

function ProjectBody({project, index}) {
    const ProjectBodyContainer = styled.div`
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
        grid-template-areas: "topLeft topRight"
                             "bottomLeft bottomRight";
        gap: 16px;
        padding:0 5%;
        margin-bottom:100px;
        .project__info{
            .project__type{
                font-weight: 500;
                color: ${project.color};
            }
            .project__name{
                font-weight: 500;
                margin-top: 4px;
                color: #262F38;
            }
            .project__desc{
                margin: 20px 0;
                width:80%;
                color: #262F38;
            }
            .project__link{
                text-decoration: none;
                padding: 10px 16px;
                border: 2px solid ${project.color};
                color: ${project.color};
                background: white;
                border-radius: 30px;
                font-weight: bold;
                display: inline-block;
            }
        }
        .project__info.odd, .project__image.odd{
            grid-area: topLeft;
        }
        .project__info.even, .project__image.even{
            grid-area: topRight;
        }
        .project__image{
            position:relative;
            display:grid;
            place-content:center;
            .project__image-lazy{
                max-width: 100%;
                height: 400px;
                object-fit: contain;
                position:relative;
                z-index:9;
            }
            &:before{
                content: '';
                display: block;
                position: absolute;
                top: -24px;
                left: 0;
                background: ${project.color};
                opacity: 0.3;
                width: 250px;
                height: 250px;
                border-radius: 50%;
                z-index: 0;
            }
            &:after{
                content: '';
                display: block;
                position: absolute;
                bottom: 20px;
                right: 50px;
                background: ${project.color};
                opacity: 0.3;
                width: 150px;
                height: 150px;
                border-radius: 50%;
                z-index: -1;
            }
        }
        ${breakPoint('sm')}{
            margin: 120px auto;
            .project__image{
                grid-area: topLeft !important;
                margin-bottom:24px;
            }
            .project__info{
                grid-area: bottomLeft !important;
                .project__desc{
                    width:100%;
                }
            }
        }
    `

    useEffect(()=>{
        AOS.init({
            duration : 600
        });
    },[])

    return (
        <ProjectBodyContainer>
            <div data-aos="fade-up" className={`project__info ${index%2===1 ? 'even':'odd'}`}>
                <p className="project__type">{project.type}</p>
                <h1 className="project__name">{project.name}</h1>
                <p className="project__desc">{project.desc}</p>
                <a href={project.link} target="_blank" rel="noopener" className="project__link" variant="outlined">Live Demo</a>
            </div>
            <div data-aos="fade-up" className={`project__image ${index%2===1 ? 'odd':'even'}`}>
                <LazyLoadImage
                    className="project__image-lazy"
                    alt={project.name}
                    effect="blur"
                    src={project.image} />
            </div>
        </ProjectBodyContainer>
    )
}

export default ProjectBody
