import React,{Fragment, useEffect} from 'react'
import pokemonGoImage from '../assets/img/pokemon_go.png'
import laLearnImage from '../assets/img/la_learn.png'
import skyChatImage from '../assets/img/sky_chat.png'
import weFoodImage from '../assets/img/WeFood.png'
import weMovieImage from '../assets/img/weMovie.png'
import ProjectBody from './ProjectBody'
import styled from '@emotion/styled'
import bottomGapDeepBlue from '../assets/img/bottom_gat_deepblue.svg'
import "aos/dist/aos.css";
import AOS from 'aos';

function Projects() {

    const projects = [
        {
            type:'Personal Project',
            name:'Pokemon GO',
            desc:'Progressive web app that can be used to browse pokemon and catch them for collection. I made this using React.js, Material UI, Apollo GraphQL, and for data i using from pokeapi.co',
            image: pokemonGoImage,
            color: '#3f51b5',
            link: 'https://pokemon-go-a6d13.web.app'
        },
        {
            type:'Submission Dicoding: Menjadi Front-End Web Developer Expert',
            name:'WeFood',
            desc:'A progressive web application to find the best restaurants from all over Indonesia, I made this using web component, webpack, workbox, karma, and for data i using from dicoding-restaurant-api.',
            image: weFoodImage,
            color: '#FFC107',
            link: 'https://wefood95.web.app/'
        },
        {
            type:'Submission Dicoding: Fundamental Front-End Web Development',
            name:'WeMovie',
            desc:'website for watching movie trailers from around the world, I made this using css framework bootstrap, web component, webpack, and the spa concept but without the js framework, this project I created purely using browser api. "This product uses the TMDb API but is not endorsed or certified by TMDb."',
            image: weMovieImage,
            color: '#1b1c35',
            link: 'https://wemovie99.web.app/'
        },
        {
            type:'Winner Up IBM New Collar and Skills Accelerator Center 2020',
            name:'LaLearn',
            desc:'An application that can be used to help translate text, audio, and images from one language to another. I made this using Ionic framework, Angular, Firebase auth, Firebase firestore, and IBM Cloud.',
            image: laLearnImage,
            color: '#28df99',
            link: 'https://drive.google.com/file/d/1YZCpdHHAHM-5jpq9-pr3D4TCQ2b0Wx-i/view?usp=sharing'
        },
        {
            type:'Personal Project',
            name:'Sky Chat',
            desc:'An application that can be used to chat in real time, you can also add friends and create your own group. I made this using Ionic framework, Angular, Firebase auth, Firebase firestore, Firebase cloud messaging',
            image: skyChatImage,
            color: '#E41D6F',
            link: 'https://drive.google.com/file/d/1Bxui99vecmIFwDAP7wVykzS4D6T4qXa-/view?usp=sharing'
        }
    ]

    const ProjectsContainer = styled.div`
        background: rgba(240,240,240,0.1);
        position:relative;
        padding: 60px 0 200px 0;
        .tag__container{
            display:flex;
            justify-content:center;
            margin-bottom: 120px;
            .tag__project{
                display:inline-block
                position:relative;
                &:after{
                    content: '';
                    display: block;
                    margin: 10px auto;
                    width: 80%;
                    height: 3px;
                    background: #616161;
                }
            }
        }
        .bottom-gap-deep-blue{
            position:absolute;
            bottom:-1px;
            right:0;
            width:100%;
        }
    `

    useEffect(()=>{
        AOS.init({
            duration : 600
        });
    },[])

    return (
        <ProjectsContainer>
            <div className="tag__container" id="project" data-aos="fade-up">
                <h2 className="tag__project">PROJECTS</h2>
            </div>
            {
                projects.map((project,index)=>(
                    <ProjectBody project={project} index={index} key={index} />
                ))
            }
            <img src={bottomGapDeepBlue} className="bottom-gap-deep-blue" alt="bottom gap deep blue" />
        </ProjectsContainer>
    )
}

export default Projects
